$(document).ready(function() {
	$('#fullpage').fullpage({
        anchors:['header', 'read', 'show', 'subscribe'],
        loopBottom: true,
        menu: '#menu',
     afterLoad: function(anchorLink, index){
      let intro = $('.header__intro');
      let media = $('.header__media');
      let reads = $('.reads__content .reads__wrap');
      let block1 = $('.present-gallery__part_js-1');
      let block2 = $('.present-gallery__part_js-2');
      let block3 = $('.present-gallery__part_js-3');
      let block4 = $('.present-gallery__part_js-4');
      let feedback = $('.feedback__content');
         switch(index) {
           case 1:
           intro.restart().timeScale(1);
           media.restart().timeScale(1);
           contentReveal();
				break;
           case 2: 
           
           reads.restart().timeScale(1);
           contentReveal();
				break;
           case 3:
           
           block1.restart().timeScale(1);
           block2.restart().timeScale(1);
           block3.restart().timeScale(1);
           block4.restart().timeScale(1);
           contentReveal();
				break;
       case 4:
       
       feedback.restart().timeScale(1);
       contentReveal();
				break;
         }
     }
  });
  
});


